<template>
  <div>
    <h2>最简单的js检测代码</h2>
    <pre><code>
      if (/Android|webOS|Windows Phone|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        window.location.href = url
      }
    </code></pre>

    <h2>平台,设备和操作系统</h2>
    <pre><code>
      var system = {
        win: false,
        mac: false,
        xll: false,
        ipad:false
      }
      var p = navigator.platform
      system.win = p.indexOf("Win") == 0
      system.mac = p.indexOf("Mac") == 0
      system.x11 = (p == "X11") || (p.indexOf("Linux") == 0)
      system.ipad = (navigator.userAgent.match(/iPad/i) != null) ? true : false
      if (system.win || system.mac || system.xll||system.ipad) {

      }
    </code></pre>

    <h2>获取浏览器类型</h2>
    <pre><code>
      var browser = {
        version: function () {
          var u = navigator.userAgent, app = navigator.appVersion
          return {
            trident: u.indexOf('Trident') > -1,
            presto: u.indexOf('Presto') > -1,
            webKit: u.indexOf('AppleWebKit') > -1,
            gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1,
            mobile: !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/),
            ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
            android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1,
            iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1,
            iPad: u.indexOf('iPad') > -1,
            webApp: u.indexOf('Safari') == -1,
            QQbrw: u.indexOf('MQQBrowser') > -1,
            weiXin: u.indexOf('MicroMessenger') > -1,
            ucLowEnd: u.indexOf('UCWEB7.') > -1,
            ucSpecial: u.indexOf('rv:1.2.3.4') > -1,
            ucweb: u.indexOf('UC') > -1,
            Symbian: u.indexOf('Symbian') > -1,
            ucSB: u.indexOf('Firefox/1.') > -1
          }
        }()
      }
    </code></pre>

    <h2>使用device.js检查设备</h2>
    <pre><code>
      // 使用方法
      if (device.mobile()) {
        window.location.href = url
      }

      (function() {
        var device, find, userAgent;
        device = {}
        window.device = device

        // Lowercase, so we can use the more efficient indexOf(), instead of Regex
        userAgent = window.navigator.userAgent.toLowerCase()

        device.ios = function () {
          return device.iphone() || device.ipod() || device.ipad()
        }

        device.iphone = function () {
          return !device.windows() && find('iphone')
        }

        device.ipod = function () {
          return find('ipod')
        }

        device.ipad = function () {
          return find('ipad')
        }

        device.android = function () {
          return !device.windows() && find('android')
        }

        device.androidPhone = function () {
          return device.android() && find('mobile')
        }

        device.androidTablet = function () {
          return device.android() && !find('mobile')
        }

        device.windows = function () {
          return find('windows')
        }

        device.windowsPhone = function () {
          return device.windows() && find('phone')
        }

        device.windowsTablet = function () {
          return device.windows() && (find('touch') && !device.windowsPhone())
        }

        device.fxos = function () {
          return (find('(mobile;') || find('(tablet;')) && find('; rv:')
        }

        device.fxosPhone = function () {
          return device.fxos() && find('mobile')
        }

        device.fxosTablet = function () {
          return device.fxos() && find('tablet')
        }

        device.nodeWebkit = function () {
          return typeof window.process === 'object'
        }

        device.mobile = function () {
          return device.androidPhone() || device.iphone() || device.ipod() || device.windowsPhone() || device.fxosPhone()
        }

        device.tablet = function () {
          return device.ipad() || device.androidTablet() || device.windowsTablet() || device.fxosTablet()
        }

        device.desktop = function () {
          return !device.tablet() && !device.mobile()
        }

        device.television = function() {
          var i, television = [
            "googletv",
            "viera",
            "smarttv",
            "internet.tv",
            "netcast",
            "nettv",
            "appletv",
            "boxee",
            "kylo",
            "roku",
            "dlnadoc",
            "roku",
            "pov_tv",
            "hbbtv",
            "ce-html"
          ];

          i = 0;
          while (i < television.length) {
            if (find(television[i])) {
              return true
            }
            i++;
          }
          return false
        }

        device.portrait = function () {
          return (window.innerHeight / window.innerWidth) > 1
        }

        device.landscape = function () {
          return (window.innerHeight / window.innerWidth) < 1
        }

        find = function (needle) {
          return userAgent.indexOf(needle) !== -1
        }

        window.device = device
      }).call(this)
    </code></pre>
  </div>
